/**
 *id
 *
 * @param {*} id
 * @param {*} layers
 * @return {*}
 */
var getLayer = (id, layers) => {
  for (let i = 0; i < layers.length; i++) {
    if (layers[i] && L.Util.stamp(layers[i]) === id) {
      return layers[i];
    }
  }
};

